.manageTask {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 10px;
}
.manageTask_title {
  text-transform: uppercase;
}
@media only screen and (min-width: 768px) {
  .manageTask {
    flex-direction: row;
    width: 100%;
    justify-content: space-evenly;
  }
}
